.rating {
  position: relative;
  display: flex;
  align-items: center;
  width: 50%;
  margin-top: 30px;
  padding: 25px 0;

  border-bottom: 0.5px solid #c7c7c7;
  border-top: 0.5px solid #c7c7c7;

  @media (max-width: $tablet-width) {
    width: 100%;
    justify-content: center;
  }

  @media (max-width: $mobile-width) {
    flex-direction: column;
    align-items: flex-start;
    margin-top: 25px;
    padding: 12px 0;
    padding-left: 50px;
  }

  p {
    margin: 0 15px;
    font-size: 18px;
    line-height: 26px;
    letter-spacing: 1px;

    @media (max-width: $mobile-width) {
      margin: 0;
      font-size: 14px;
    }
  }
}

.rating--noborder {
  border: none;
  margin-left: 30px;
  margin-top: 0;
  padding-top: 35px;
  width: 60%;

  @media (max-width: $mobile-width) {
    margin-left: 15px;
    width: 270px;
  }
}

.rating-img-cup {
  width: 32px;
  height: 36px;

  @media (max-width: $mobile-width) {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
  }
}

.rating-img {
  width: 118px;
  height: 17px;
}
